




















import { Component, Vue, Prop } from "vue-property-decorator";
import { isMobile } from "../../../platform";
import NestNode from "./VNestNode.vue";

@Component({
    name: "VNestList",
    components: {
        NestNode,
    },
})
export default class VNestList extends Vue {
    @Prop({ default: [] }) private items!: Array<any>; // 
    @Prop({ default: true }) private isRoot!: boolean; // 
    @Prop({ type: [Number, String], default: "200px" }) private width: any; // 
    @Prop({ default: 24 }) private iconSize!: number; // 
    @Prop({ default: "text" }) private textField!: string; // 
    @Prop({ default: "icon" }) private iconField!: string; // 
    @Prop({ default: "sublist" }) private sublistField!: string; // 
    @Prop({ default: "click" }) private clickField!: string; // 

    dData: any = null; // 
    dIsMobile: boolean = isMobile; // 

    // sheetelevation
    get cSheetElevation() {
        // 0
        if (this.dIsMobile && !this.isRoot) {
            return 0;
        }
        return 10;
    }
    // sheet
    get cSheetWidth() {
        // PCwidth
        if (this.isRoot || !this.dIsMobile) {
            return this.width;
        }
        return "100%";
    }

    // 
    SetData(data: any) {
        this.dData = data;
    }
    // NestNode
    ItemClick(event: any) {
        this.$emit("itemclick", event);
    }
}
