





































import { Component, Vue, Prop, Watch } from "vue-property-decorator";

const color: any = {
    // 
    success: ["#f0f9eb", "#e1f3d8", "#67C23A"],
    info: ["#edf2fc", "#EBEEF5", "#909399"],
    warning: ["#fdf6ec", "#faecd8", "#E6A23C"],
    error: ["#fef0f0", "#fde2e2", "#F56C6C"],
};

@Component({
    name: "VMessage",
})
export default class VMessage extends Vue {
    visible = false;
    message = ""; // 
    duration = 2000; // ,  0 
    type = "info"; // 
    customClass = ""; // 
    onClose: Function | undefined = undefined; // ,  message 
    showClose = true; // 
    closed = false;
    verticalOffset = 20; // Message 
    timer: number | undefined = undefined;
    dangerouslyUseHTMLString = false; //  message  HTML 
    center = false; // 

    // 
    get positionStyle() {
        return {
            top: this.verticalOffset + "px",
        };
    }
    // 
    get colorStyle() {
        return {
            "background-color": color[this.type][0],
            "border-color": color[this.type][1],
            color: color[this.type][2],
        };
    }
    // 
    get iconType() {
        switch (this.type) {
            case "success":
                return "mdi-check-circle";
            case "info":
                return "mdi-information";
            case "warning":
                return "mdi-alert-circle";
            case "error":
                return "mdi-close-circle";
        }
        return "mdi-information";
    }

    @Watch("closed")
    closedWatch(newVal: boolean) {
        if (newVal) {
            this.visible = false;
        }
    }

    // 
    handleAfterLeave() {
        this.$destroy(); // true
        (this.$el as any).parentNode.removeChild(this.$el);
    }
    // 
    close() {
        this.closed = true;
        if (typeof this.onClose === "function") {
            this.onClose(this);
        }
    }
    // 
    clearTimer() {
        clearTimeout(this.timer);
    }
    // 
    startTimer() {
        if (this.duration > 0) {
            this.timer = setTimeout(() => {
                if (!this.closed) {
                    this.close();
                }
            }, this.duration);
        }
    }
    // 
    keydown(e: KeyboardEvent) {
        if (e.keyCode === 27) {
            // esc
            if (!this.closed) {
                this.close();
            }
        }
    }

    mounted() {
        this.startTimer();
        document.addEventListener("keydown", this.keydown);
    }
    beforeDestroy() {
        document.removeEventListener("keydown", this.keydown);
    }
}
