









































































import { Component, Vue, Prop, Watch } from "vue-property-decorator";
import VWindowManager from "./VWindowManager";

@Component({
    name: "VPopupWindow",
})
export default class VPopupWindow extends Vue {
    @Prop() private id!: string; // id
    @Prop({ default: false }) private value!: boolean; // 
    @Prop({ default: "" }) private title!: string; // 
    @Prop() private icon?: string; // 
    @Prop() private iconimg?: string; // 
    @Prop({ default: "50%" }) private width!: string; // 
    @Prop({ default: "50%" }) private height!: string; // 
    @Prop({ type: [String, Array], default: "auto" }) private position: any; // 
    @Prop({ type: [String, Array], default: "10px" }) private margin: any; // positionrbl
    @Prop({ default: false }) private minimize!: boolean; // 
    @Prop({ default: false }) private maximize!: boolean; // 
    @Prop({ default: true }) private closeAble!: boolean; // 
    @Prop({ default: false }) private moveAble!: boolean; // 
    @Prop({ default: false }) private fullscreen!: boolean; // 
    @Prop({ default: "" }) private src!: string; // 
    @Prop({ type: [Object, Function], default: null }) private component?: any; // 
    @Prop({
        default: () => {
            /**/
        },
    })
    private componentProps?: Record<string, any>; // 
    @Prop({
        default: () => {
            /**/
        },
    })
    private componentEvents!: Record<string, Function>; // 
    @Prop({ type: [String, Number], default: 0 }) private shade: any; // 
    @Prop({ default: false }) private shadeClose!: boolean; // (shadeEventtrue)
    @Prop({ default: false }) private shadeEvent!: boolean; // 
    @Prop() private beforeClose!: Function; // 
    @Prop() private afterClose!: Function; // 
    @Prop({ default: 2 }) private elevation!: number; // 

    dWindowMoving = false; // 
    dTopMoved = ""; // top
    dLeftMoved = ""; // left
    dIsMaximize = false; // 
    dIsMinimize = false; // 
    dShow = this.value; // value
    instances = VWindowManager.getInstances();

    // css4
    get cMargin() {
        const arr = new Array(4);
        const margin = this.margin;
        if (typeof margin === "object") {
            switch (margin.length) {
                case 1:
                    arr.fill(margin[0]);
                    break;
                case 2:
                    arr[0] = arr[2] = margin[0];
                    arr[1] = arr[3] = margin[1];
                    break;
                default:
                    arr.map((item, index) => {
                        arr[index] = margin[index] || "0px";
                    });
                    break;
            }
        } else {
            arr.fill(margin);
        }
        return arr;
    }
    // 
    get cWidth() {
        return this.width;
    }
    // 
    get cHeight() {
        return this.height;
    }
    // --top
    get cTop() {
        if (this.dTopMoved) return this.dTopMoved;
        const position = this.position;
        if (typeof position === "object") {
            if (position.length === 2) {
                return position[0];
            }
        } else {
            switch (position) {
                case "t":
                case "lt":
                case "rt":
                    return `${this.cMargin[0]}`;
                case "b":
                case "lb":
                case "rb":
                    return `calc(100% - ${this.cHeight} - ${this.cMargin[2]})`;
                case "l":
                case "r":
                case "auto":
                    return `calc((100% - ${this.cHeight})/2)`;
                default:
                    return position;
            }
        }
        return `calc((100% - ${this.cHeight})/2)`;
    }
    // --left
    get cLeft() {
        if (this.dLeftMoved) return this.dLeftMoved;
        const position = this.position;
        if (typeof position === "object") {
            if (position.length === 2) {
                return position[1];
            }
        } else {
            switch (position) {
                case "l":
                case "lt":
                case "lb":
                    return `${this.cMargin[3]}`;
                case "r":
                case "rt":
                case "rb":
                    return `calc(100% - ${this.width} - ${this.cMargin[1]})`;
                case "t":
                case "b":
                case "auto":
                    return `calc((100% - ${this.width})/2)`;
            }
        }
        return `calc((100% - ${this.width})/2)`;
    }
    // 
    get cPositionStyle() {
        let style;
        if (this.dIsMinimize) {
            style = {
                width: "200px",
                height: "40px",
                bottom: "0px",
                left: "0px",
            };
        } else if (this.dIsMaximize) {
            style = {
                width: "100%",
                height: "100%",
                top: "0px",
                left: "0px",
            };
        } else {
            style = {
                width: this.width,
                height: this.height,
                top: this.cTop,
                left: this.cLeft,
            };
        }
        return style;
    }
    // 
    get cTitleStyle() {
        let btnCount = 0;
        if (this.minimize && !this.dIsMinimize) btnCount++;
        if (
            this.maximize &&
            ((!this.dIsMinimize && this.dIsMaximize) ||
                (this.dIsMinimize && !this.dIsMaximize))
        )
            btnCount++;
        if (
            this.maximize &&
            ((!this.dIsMinimize && !this.dIsMaximize) ||
                (this.dIsMinimize && this.dIsMaximize))
        )
            btnCount++;
        if (this.closeAble) btnCount++;
        return {
            cursor:
                this.moveAble && !this.dIsMinimize && !this.dIsMaximize
                    ? "move"
                    : "default",
            "padding-left": this.icon || this.iconimg ? "40px" : "10px",
            "padding-right": btnCount * 40 + "px",
        };
    }
    // 
    get cShadeStyle() {
        const style: any = {};
        style["pointer-events"] = this.shadeEvent ? "auto" : "none";
        if (typeof this.shade === "number") {
            style.background = "#000";
            style.opacity = this.shade;
        } else {
            style.background = this.shade;
        }
        return style;
    }
    // z-index
    get cZIndexStyle() {
        const zIndex = this.instances.findIndex(
            (instance) => instance.id === this.id
        );
        return {
            "z-index": VWindowManager.zIndexBase + zIndex,
        };
    }
    // 
    get cMinimize() {
        // 
        return this.minimize && !this.dIsMinimize;
    }
    // /
    get cNormalizate() {
        if (this.dIsMinimize) {
            // 
            return true;
        } else {
            // maximize
            return this.maximize;
        }
    }

    @Watch("value")
    valueWatch(newVal: boolean) {
        this.dShow = newVal;
    }

    // 
    Minimize() {
        this.dIsMinimize = true;
    }
    // 
    Maximize() {
        if (this.dIsMinimize) {
            this.dIsMinimize = false;
        } else {
            this.dIsMaximize = !this.dIsMaximize;
        }
    }
    // 
    Show(): void {
        this.dShow = true;
        this.$emit("input", true);
    }
    // 
    Close(data?: any) {
        const close = () => {
            this.dShow = false;
            this.$emit("input", false);
            if (typeof this.afterClose === "function") {
                this.afterClose(data);
            }
        };
        if (typeof this.beforeClose === "function") {
            this.beforeClose(close, data);
        } else {
            close();
        }
    }
    // 
    TitleMouseDown(e: any) {
        if (!this.moveAble || this.dIsMinimize || this.dIsMaximize) return;
        this.dWindowMoving = true;
        const elWindow = e.currentTarget.parentNode;
        const disX = e.clientX - elWindow.offsetLeft;
        const disY = e.clientY - elWindow.offsetTop;
        const onmousemove = document.onmousemove;
        const onmouseup = document.onmouseup;
        document.onmousemove = (e) => {
            this.dLeftMoved = e.clientX - disX + "px";
            this.dTopMoved = e.clientY - disY + "px";
        };
        document.onmouseup = () => {
            this.dWindowMoving = false;
            document.onmousemove = onmousemove;
            document.onmouseup = onmouseup;
        };
    }
    // 
    ShadeClick() {
        if (this.shadeClose) {
            this.Close();
        }
    }
    // 
    SetTop() {
        VWindowManager.setTop(this.id);
    }

    mounted() {
        this.dIsMaximize = this.fullscreen;
        // 
        if (this.component && this.componentEvents) {
            const interval = setInterval(() => {
                if (this.$refs.COM) {
                    const comRef = this.$refs.COM as Vue;
                    Object.keys(this.componentEvents).forEach((name) => {
                        if (this.componentEvents[name]) {
                            comRef.$on(name, this.componentEvents[name]);
                        }
                    });
                    clearInterval(interval);
                }
            }, 100);
        }
    }
}
